import React, {Component} from 'react';
import {HashRouter, Route, Switch} from 'react-router-dom';
import {Home} from './Home';
import {AboutARC} from './AboutARC';
import {AboutSRC} from './AboutSRC';
import {AboutTheBoard} from './AboutTheBoard';
import {Contact} from './Contact';
import {NoMatch} from './NoMatch';
import {Layout} from './components/Layout';
import {NavigationBar} from './components/NavigationBar';

class App extends Component {
  render(){
    return (
      <React.Fragment>
        <NavigationBar />
        <Layout>
          <HashRouter>
            <Switch>
              <Route exact path="/" component={Home} />
              <Route exact path="/aboutarc" component={AboutARC} />
              <Route exact path="/aboutsrc" component={AboutSRC} />
              <Route exact path="/abouttheboard" component={AboutTheBoard} />
              <Route exact path="/contact" component={Contact} />
              <Route component={NoMatch} />
            </Switch>
          </HashRouter>
        </Layout>
      </React.Fragment>
    );
  }
}

export default App;
